<template>
	<div class="read" :class="{yj:clearActive === 1, hy:clearActive === 2}">
		<div class="read-content" @click="conentMenu">
			<h1>{{this.$route.params.id}}</h1>
			<div class="inner" ref="inners">
				<p>{{reading.body}}</p>
			</div>
		</div>
		
		
		<div class="reading-page">
			<transition name="drawer">
				<div class="reading-bar drawer" v-show="content">
					<i class="back" @click="routerGo">
						返回
					</i>
					<h1>{{this.$route.params.name}}</h1>
					<span></span>
				</div>
			</transition>
			
			<transition name="drawerul">
				<ul class="drop-down" v-show="content">
					<li>
						<router-link to="/">首页</router-link>
					</li>
					<li>
						<router-link to="/bookshelf">书架</router-link>
					</li>
					<li @click="routerGo">详情</li>
				</ul>
			</transition>
			
			<transition name="scale">
				<div class="drop-bottom scale" v-show="content">
					<div class="item">
						<span class="item-f" @click="reduce">Aa-</span>
						<span class="item-bar">
							<span class="content" ref="cont"></span>
						</span>
						<span class="item-r" @click="add">Aa+</span>
					</div>
					
					<div class="drop-clearfix" >
						<span v-for="(clearfix, list) in clearfix" :klisty="list"
							@click="clearA(list)"
							:class="{dropactive:clearActive == list}">
							{{clearfix}}
						</span>
					</div>
					
					<div class="directory">
						<span class="dir-active">上一章</span>
						<span>目录</span>
						<span>下一章</span>
					</div>
				</div>
			</transition>
		</div>
	
	</div>
</template>

<script>

	export default {
		name:"Read",
		data(){
			return{
				reading:[],
				content:false,
				clearActive: 0,
				clearfix:[
					'默认',
					'夜间',
					'护眼'
				],
				font: 20,
				width: 50,
				listBody: []
			}
		},
		beforeRouteEnter(to,from,next){
			next(vm => {
				vm.axios.get(vm.Url).then(r => {
					vm.reading = r.data.chapter
				})
			})
		},
		methods:{
			routerGo(){
				this.$router.go(-1);
			},
			conentMenu(){
				this.content = !this.content
			},
			clearA(list){
				this.clearActive = list
			},
			reduce(){
				let font = this.$refs.inners
				let con = this.$refs.cont
				if(this.font > 16){
					this.font -= 2
					this.width -= 25 
					font.style.fontSize = this.font + 'px'
					con.style.width = this.width + '%'
				}
			},
			add(){
				let font = this.$refs.inners
				let con = this.$refs.cont
				if(this.font < 24){
					this.font += 2
					this.width += 25
					font.style.fontSize = this.font + 'px'
					con.style.width = this.width + '%'
				}
			},
			
		},
		computed:{
			Url(){
				let esc = `${this.$route.params.link}`
				
				// 转码	escape()
				let url = "a/chapter/" + escape(esc)
				return url
			},
		}
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import "../../public/sass/mixin";
	a{
		color: #fff;
	}
	.read{
		position: absolute;
		z-index: 100;
		left: 0;
		top: 0;
		right: 0;
		z-index: 100;
		color: #5c5d58;
		background-color: #eee6dd;
	}
	.yj{
		background-color: #0C0C0C;
		color: #666;
	}
	.hy{
		background-color: #b8cd8d;
		color: #2F4E29;
	}
	.read-content{
		width: 100%;
		height: 100%;
		padding: 1.5625rem 1.25rem;
		
		h1{
			font-size: 1.5rem;
			line-height: 1.5rem;
			font-weight: 400;
			margin-bottom: 1.25rem;
			color: #333;
		}
		
		.inner{
			font-size: 20px;
			line-height: 1.75rem;
			
			p{
				text-indent: 2em;
				text-align: justify;
				margin: 0.625rem 0;
				white-space: pre-line;
			}
		}
	}


	.reading-page{
		color: #fff;
	}
	.reading-bar{
		position: fixed;
		background: rgba(50,51,52,.9);
		top: 0;
		left: 0;
		right: 0;
		display: flex;
		justify-content: space-between;
		align-items: center;
		
		i{
			width: 44px;
			height: 2.75rem;
			background: url(../../public/img/arror-back.png) no-repeat;
			background-size: 42%;
			background-position: left;
			line-height: 2.75rem;
			text-align: right;
			font-size: 0.75rem;
			margin-left: 0.3125rem;
		}
		
		h1{
			height: 2.75rem;
			line-height: 2.75rem;
			font-size: 0.75rem;
		}
		
		span{
			width: 60px;
			height: 44px;
			display: inline-block;
			background: url(../../public/img/shu.png) no-repeat center;
			background-size: 25%;
		}
	}

	.drop-down{
		position: fixed;
		background: #323334;
		border-radius: 3px;
		color: #fff;
		top: 8%;
		right: 4%;
		
		li{
			border-bottom: 1px solid #222;
			width: 4.625rem;
			height: 29px;
			text-align: center;
			line-height:  1.8125rem;
			font-size: 0.75rem;
		}
		
		li:nth-child(3){
			border-bottom: none;
		}
	}
	
	.drop-bottom{
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		height: 10.625rem;
		background-color: rgba(50,51,52,.9);
		padding: 1.25rem;
	}
	
	.item{
		line-height: 1.875rem;
		font-size: 18px;
		height: 1.875rem;
		
		.item-f{
			float: left;
		}
		.item-r{
			float: right;
		}
		.item-bar{
			float: left;
			height: 2px;
			width: 240px;
			background: #b2b2b2;
			margin: 15px 14px;
			
			.content{
				background-color: #b93221;
				height: 0.125rem;
				width: 50%;
				display: block;
				
			}
		}
	}
	
	.drop-clearfix{
		display: flex;
		width: 100%;
		height: 30px;
		margin: 20px 0;
		
		span{
			flex: 0 0 100px;
			height: 30px;
			color: #fff;
			border: 1px solid #535353;
			border-radius: 0.125rem;
			margin-right: 1.0625rem;
			text-align: center;
			line-height: 1.875rem;
			font-size: 0.875rem;
			float: left;
		}
		
		.dropactive{
			color: #b93221;
			border: 1px solid #b93221;
		}
	}
	
	.directory{
		width: 100%;
		height: 1.875rem;
		display: flex;
		justify-content:space-between;
		
		span{
			flex: 0 0 50px;
			text-align: center;
			line-height: 1.875rem;
			font-size: 14px;
		}
		
		.dir-active{
			color: #a8a8a8;
		}
	}

	
	.scale-enter-active, .scale-leave-active{
		transition: all 0.2s linear;
	}
	.scale-enter, .scale-leave-to{
		transform: translateY(170px);
		opacity: 0;
	}
	.scale-enter-to, .scale-leave{
		transform: translateY(0);
		opacity: 1;
	}
	
	
	.drawerul-enter-active, .drawerul-leave-active {
	    transition: all 0.2s linear;
	}
	.drawerul-enter, .drawerul-leave-to {
		transform: translateY(-100%);
		opacity: 0;
	}
	.drawerul-enter-to, .drawerul-leave {
		transform: translateY(0);
		opacity: 1;
	}
	
	
	.drawer-enter-active, .drawer-leave-active {
        transition: all 0.2s linear;
	}
	.drawer-enter, .drawer-leave-to {
		transform: translateY(-100%);
		opacity: 0;
	}
	.drawer-enter-to, .drawer-leave {
		transform: translateY(0);
		opacity: 1;
	}
	
	
</style>
